.page-header {
  background-color: #ffffff;
}

.page-header__logo {
  position: absolute;
  top: 223px;
  left: 50%;
  transform: translateX(-50%);

  display: block;

  width: 103px;
  height: 85px;
}

@media (min-width: 768px) {
  .page-header__logo {
    top: 0;

    width: 104px;
    height: 83px;
  }
}

@media (min-width: 1200px) {
  .page-header {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }

  .page-header__logo {
    width: 190px;
    height: 98px;
  }
}
